.learning {
 height: auto;
 padding: 1rem;
}

.color-strong {
  color: #df168b;
}

.learning_header {
  padding: 10px 20px;

  h2 {
    font-family: 'Poiret One', cursive;
    font-size: 30px;
    font-weight: bold;
  }

  p {
    font-size: 18px;
    font-weight: normal;
    text-align: justify;
    margin-bottom: 10px;
  }
}

.learning_title {
 background: rgba(255,255,255,0.9);
 padding: 0.5rem 1rem;
 border-radius: 15px;
 box-shadow: 0px 0px 10px 0px #000;

 font-family: 'Poiret One', cursive;
 font-size: 20px;

 h2 {
  font-weight: 900;
 }

 p {
   font-weight: bold;
   text-align: justify;
 }
}

.learning_stat {
 position: absolute;
 left: 0;
 width: 100%;
 background: rgba(255,255,255,0.95);
 margin-top: 2%;
 padding: 1rem;
 box-shadow: 0px 0px 10px 0px #000;
 display: flex;
 justify-content: center;
 align-items: center;
 h2 {
  font-weight: 900;
  padding: 0 1rem;
 }
 span {
  padding: 0 2rem 0 1rem;
 }
}

.learning_cards {
 width: 100%;
 display: flex;
 justify-content: space-evenly;
 background: rgba(255,255,255,0.85);
 padding: 1rem;
 margin-top: 8.5rem;
 border-radius: 15px;
 box-shadow: 0px 0px 10px 0px #000;
 h2 {
  font-weight: 900;
 }
 div {
  background: #fff;
  width: 25%;
  padding: 2rem;
  border-radius: 15px;
  box-shadow: 0px 0px 10px 0px rgba(0,0,0,0.4);
  align-items: center;
 }
 button {
      display: block;
      padding: 10px 20px;
      background-color: #FFF;
      background-color: #333470;
      border: none;
      border: 1px solid #333470;
      border-radius: 10px;
      font-size: 18px;
      color: #333470;
      color: #FFF;
      letter-spacing: 2px;
      transition: background-color .3s;
      cursor: pointer;
 }
 button:hover {
  background-color: #333470;
  background-color: #FFF;
  color: #FFF;
  color: #333470;
}

button:focus {
  outline: none;
}
}

@media screen and (max-width: 550px) {
  .learning  {
    padding: 20px;
  }

  .learning_header {
    font-size: 16px;

    h2 {
      font-size: 20px;
    }

    p {
      font-size: 16px;
    }
  }
}
